<template>
  <div style="min-height: 90vh;">
    <v-row no-gutters>
      <v-col cols="12" md="2">
        <v-sheet
          color="grey lighten-3"
          width="100%"
          height="100vh"
          class="px-4 ma-0 pt-8"
        >
          <div class="text-body-2">Sort by</div>
          <v-radio-group v-model="SortBy">
            <v-radio
              dense
              class="ml-4"
              color="secondary"
              v-for="(item, i) in sortItmes"
              :key="i"
              :label="item.label"
              :value="item.value"
            ></v-radio>
          </v-radio-group>
          <div class="text-body-2">Views</div>
          <v-slider
            v-model="viewCount"
            min="1"
            max="10000"
            thumb-label
            color="secondary"
            track-color="grey lighten-1"
          ></v-slider>
          <div class="text-body-2">Price ($)</div>
          <v-slider
            v-model="price"
            min="10"
            max="100"
            thumb-label
            color="secondary"
            track-color="grey lighten-1"
          ></v-slider>
          <div class="text-body2">Type</div>
          <v-radio-group v-model="type">
            <v-radio
              dense
              class="ml-4"
              color="secondary"
              v-for="(item, i) in typeItems"
              :key="i"
              :label="item.label"
              :value="item.value"
            ></v-radio>
          </v-radio-group>
        </v-sheet>
      </v-col>
      <v-col cols="12" md="10">
        <v-sheet color="grey lighten-4" width="100%" class="pa-4">
          <v-row no-gutters justify="space-between">
            <v-col cols="5" md="3">
              <div class="text-body-2 mb-2">
                Category
              </div>
              <v-autocomplete
                placeholder="Select a category or type"
                spellcheck="off"
                v-model="category"
                :items="categoryItems"
                solo
                dense
                clearable
                color="grey darken-1"
                hide-details
              ></v-autocomplete>
            </v-col>
            <v-col cols="5" md="3">
              <v-text-field
                class="text-body-2 mt-8"
                type="text"
                placeholder="Search .."
                prepend-icon="mdi-magnify"
                v-model="search"
                solo
                dense
                hide-details
                color="grey darken-1"
                clearable
              ></v-text-field>
            </v-col>
          </v-row>
        </v-sheet>
        <v-row>
          <v-col cols="6" md="9" class="d-flex align-center">
            <div
              class="text-caption ml-4 mt-4 no-select animate__fadeInLeft animate__animated animate__delay-3s"
            >
              <v-icon small class="mr-1">mdi-star</v-icon>
              Immerse yourself with our collection of classes and courses
            </div>
          </v-col>
          <v-col cols="6" md="3">
            <div class="d-flex justify-end align-center mx-2">
              <v-btn-toggle dense v-model="viewMode">
                <v-btn value="list">
                  <v-icon color="grey darken-1"
                    >mdi-format-list-bulleted</v-icon
                  >
                </v-btn>
                <v-btn value="grid">
                  <v-icon color="grey darken-1">mdi-grid</v-icon>
                </v-btn>
              </v-btn-toggle>
            </div>
          </v-col>
        </v-row>

        <div class="pa-4">
          <v-row
            style="min-height: 70vh;"
            v-if="isLoading"
            justify="center"
            align="center"
          >
            <v-col cols="4">
              <v-img :src="loadImg" wdith="10rem" height="10rem" contain>
                <v-progress-circular
                  indeterminate
                  color="white"
                  size="10"
                  width="2"
                  class="load-spinner"
                  v-if="$vuetify.breakpoint.mdAndUp"
                ></v-progress-circular>
              </v-img>
            </v-col>
          </v-row>
          <v-row style="min-height: 100vh;" v-else>
            <v-scroll-y-transition group v-if="viewMode == 'grid'">
              <v-col
                class="d-inline-block"
                cols="6"
                md="3"
                v-for="(item, i) in courseItems"
                :key="i"
              >
                <v-hover v-slot:default="{ hover }">
                  <v-card
                    :elevation="hover ? 6 : 2"
                    class="rounded-lg cursor-pointer"
                    style="overflow: hidden;"
                    height="17rem"
                    max-height="17rem"
                    :to="{ name: 'course-show', params: { id: item.id } }"
                  >
                    <v-img
                      class="white--text align-end"
                      height="150px"
                      :src="item.img"
                    >
                      <v-card-title
                        class="no-select"
                        v-text="item.title"
                      ></v-card-title>
                    </v-img>
                    <v-card-subtitle
                      class="pb-0 no-select"
                      v-text="item.subTitle"
                    ></v-card-subtitle>
                    <v-card-text>
                      <v-rating
                        :value="item.rate"
                        color="amber"
                        dense
                        half-increments
                        readonly
                        size="14"
                      ></v-rating>
                    </v-card-text>
                    <v-card-actions class="d-flex justify-end">
                      <v-chip
                        v-if="item.class"
                        class="white--text"
                        :color="item.class.color"
                        small
                      >
                        <v-icon
                          small
                          color="white"
                          class="mr-1"
                          v-html="item.class.icon"
                        ></v-icon>
                        {{ item.class.name }}
                      </v-chip>
                    </v-card-actions>
                  </v-card>
                </v-hover>
              </v-col>
            </v-scroll-y-transition>

            <v-scroll-y-transition group v-else>
              <v-col
                class="d-inline-block"
                cols="12"
                md="8"
                offset-md="1"
                v-for="(item, i) in courseItems"
                :key="i"
                :to="{ name: 'course-show', params: { id: item.id } }"
              >
                <v-hover v-slot:default="{ hover }">
                  <v-card
                    :elevation="hover ? 6 : 2"
                    class="rounded-lg cursor-pointer"
                    style="overflow: hidden;"
                  >
                    <v-row justify="space-between" no-gutters>
                      <v-col cols="4" md="3">
                        <v-img
                          class="white--text align-end"
                          height="150px"
                          :src="item.img"
                        >
                        </v-img>
                      </v-col>
                      <v-col cols="5" md="7">
                        <v-card-title
                          class="no-select"
                          v-text="item.title"
                        ></v-card-title>
                        <v-card-subtitle
                          class="pb-0 no-select"
                          v-text="item.subTitle"
                        ></v-card-subtitle>
                        <v-card-text class="d-flex flex-column">
                          <div class="text-body-2  " v-text="item.desc"></div>
                          <v-rating
                            class="mt-auto"
                            :value="item.rate"
                            color="amber"
                            dense
                            half-increments
                            readonly
                            size="14"
                          ></v-rating>
                        </v-card-text>
                      </v-col>
                      <v-col cols="3" md="2">
                        <v-card-actions class="d-flex justify-end align-start">
                          <v-chip
                            v-if="item.class"
                            class="white--text"
                            :color="item.class.color"
                            small
                          >
                            <v-icon
                              small
                              color="white"
                              class="mr-1"
                              v-html="item.class.icon"
                            ></v-icon>
                            {{ item.class.name }}
                          </v-chip>
                        </v-card-actions>
                      </v-col>
                    </v-row>
                  </v-card>
                </v-hover>
              </v-col>
            </v-scroll-y-transition>
          </v-row>
          <v-row>
            <v-col cols="8" offset="2">
              <v-pagination
                v-model="page"
                :length="5"
                color="grey darken-1"
                @input="load"
                class="justify-self-center mr-auto"
              ></v-pagination
            ></v-col>
            <v-col cols="2">
              <v-select
                v-model="perPage"
                :items="perPageItems"
                label="Items per page"
                dense
                solo
                color="grey darken-1"
              ></v-select
            ></v-col>
          </v-row>
          <div class="d-flex justify-center">
            <div style="width: 5rem;"></div>
          </div>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: "course-index",

  data() {
    return {
      sortItmes: [
        { label: "Recent", value: "recent" },
        { label: "Featured", value: "featured" },
        { label: "Likes", value: "likes" }
      ],
      SortBy: null,
      category: null,
      categoryItems: [
        { text: "Software Engineering", value: "software" },
        { text: "Database", value: "database" },
        { text: "Artificial intelligence", value: "ai" },
        { text: "Robotices", value: "robo" },
        { text: "Human resources", value: "hr" }
      ],
      viewCount: null,
      price: null,
      type: null,
      typeItems: [
        { label: "All", value: "all" },
        { label: "Free", value: "free" },
        { label: "Paid", value: "paid" }
      ],
      courseItems: [],
      page: null,
      perPage: 10,
      perPageItems: [5, 10, 20, 50],
      viewMode: "grid",
      search: null,
      isLoading: false,
      loadImg: require("./../../assets/loading.svg")
    };
  },

  created() {
    this.load();
  },

  methods: {
    renderItems(items) {
      items.forEach(element => {
        setTimeout(() => {
          this.courseItems.push(element);
        }, 600);
      });
    },
    async load() {
      this.isLoading = true;
      this.courseItems = [];
      let items = [];

      items = [
        {
          id: 1,
          title: "course 1",
          subTitle: "lorem",
          desc: "Ad ad exercitation elit voluptate mollit dolor est aute.",
          rate: 4.5,
          img: "https://cdn.vuetifyjs.com/images/cards/cooking.png",
          class: {
            name: "Featured",
            color: "orange",
            icon: "mdi-star"
          }
        },
        {
          id: 1,
          title: "course 1",
          subTitle: "lorem",
          desc: "Id id minim culpa minim veniam labore laboris.",
          rate: 4.5,
          img: "https://cdn.vuetifyjs.com/images/cards/cooking.png",
          class: {
            name: "New",
            color: "green",
            icon: "mdi-clock-fast"
          }
        },
        {
          id: 1,
          title: "course 1",
          subTitle: "lorem",
          desc:
            "Nulla consequat consectetur excepteur id mollit in ullamco et irure.",
          rate: 4.5,
          img: "https://cdn.vuetifyjs.com/images/cards/cooking.png",
          class: {
            name: "Top rated",
            color: "blue",
            icon: "mdi-chart-line-variant"
          }
        },
        {
          id: 1,
          title: "course 1",
          subTitle: "lorem",
          desc: "Est enim dolor ea dolor ea nulla sunt nulla ipsum voluptate.",
          rate: 4.5,
          img: "https://cdn.vuetifyjs.com/images/cards/cooking.png"
        },
        {
          id: 1,
          title: "course 1",
          subTitle: "lorem",
          desc:
            "Mollit cupidatat ut pariatur irure ea veniam deserunt et proident reprehenderit pariatur commodo ipsum sit.",
          rate: 4.5,
          img: "https://cdn.vuetifyjs.com/images/cards/cooking.png"
        }
      ];

      await setTimeout(() => {
        this.renderItems(items);
        this.isLoading = false;
      }, 2000);
    }
  }
};
</script>

<style>
.load-spinner {
  position: absolute;
  top: 28%;
  left: 30%;
}
</style>
