export default {
  namespaced: true,
  state: {
    drawerOpen: false,
    courseMenu: [
      {
        title: "Recent courses",
        to: { name: "course-index", query: { query: "recent" } },
        icon: "mdi-new-box"
      },
      {
        title: "Featured",
        to: { name: "course-index", query: { query: "featured" } },
        icon: "mdi-lightning-bolt-outline"
      },
      {
        title: "All categories",
        to: { name: "course-index" },
        icon: "mdi-tab"
      }
    ],
    aboutMenu: [
      { title: "Our vision", to: "", icon: "" },
      { title: "About WeLean", to: "", icon: "" },
      { title: "Contact us", to: "", icon: "" }
    ]
  },
  getters: {
    drawerOpen(state) {
      return state.drawerOpen;
    },
    courseMenu(state) {
      return state.courseMenu;
    },
    aboutMenu(state) {
      return state.aboutMenu;
    }
  },
  mutations: {
    toggleDrawer(state) {
      state.drawerOpen = !state.drawerOpen;
      //   localStorage.setItem("drawer", state.drawerOpen);
    },
    setDrawer(state, data) {
      state.drawerOpen = data;
    }
  },
  actions: {}
};
