<template>
  <v-navigation-drawer v-model="drawer" right app color="grey lighten-2">
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title class="title">
          WeLearn
        </v-list-item-title>
      </v-list-item-content>
    </v-list-item>

    <v-divider></v-divider>

    <v-list dense nav>
      <v-list-group
        :prepend-icon="item.icon"
        v-for="(item, i) in items"
        :key="i + 'i'"
        color="grey darken-1"
      >
        <template v-slot:activator>
          <v-list-item-title v-text="item.title"></v-list-item-title>
        </template>

        <v-list-item
          v-for="(subItem, d) in item.items"
          :key="d + 'd'"
          :to="subItem.to"
        >
          <v-list-item-action>
            <v-icon v-html="subItem.icon"></v-icon>
          </v-list-item-action>
          <v-list-item-title v-text="subItem.title"></v-list-item-title>
        </v-list-item>
      </v-list-group>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
export default {
  data() {
    return {
      items: [
        {
          title: "Cources",
          items: []
        },
        {
          title: "About",
          items: []
        },
        {
          title: "Blog",
          to: "blog"
        }
      ]
    };
  },

  computed: {
    drawer: {
      set(val) {
        this.$store.commit("ui/setDrawer", val);
      },
      get() {
        return this.$store.getters["ui/drawerOpen"];
      }
    }
  },

  methods: {
    async getItems() {
      let courses = await this.$store.getters["ui/courseMenu"];
      this.items[0].items = courses;

      let abouts = await this.$store.getters["ui/aboutMenu"];
      this.items[1].items = abouts;
    }
  },

  created() {
    this.getItems();
  }
};
</script>

<style scoped>
.theme--light.v-list-item--active::before,
.theme--light.v-list-item--active::before {
  opacity: 0 !important;
}
</style>
