<template>
  <v-app-bar
    app
    color="light-gray"
    :shrink-on-scroll="isShrink"
    :prominent="isShrink"
    elevation="1"
  >
    <div style="height: 100%;" class="d-flex align-center ml-md-16 ma-2">
      <router-link :to="{ name: 'home' }">
        <v-img contain max-height="60%" max-width="7rem" :src="logo"> </v-img>
      </router-link>
    </div>

    <v-spacer></v-spacer>

    <div
      v-if="$vuetify.breakpoint.mdAndUp"
      class="d-flex align-center"
      style="height: 100%;"
    >
      <v-menu
        transition="slide-y-transition"
        rounded="0"
        content-class="elevation-1"
        offset-y
        center
        bottom
        open-on-hover
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn text v-bind="attrs" v-on="on" class="mx-16">
            <span>
              Courses
            </span>
            <v-icon>
              mdi-chevron-down
            </v-icon>
          </v-btn>
        </template>
        <v-list dense>
          <v-list-item v-for="(item, i) in courseMenu" :key="i" :to="item.to">
            <v-icon
              v-html="item.icon"
              color="grey lighten-1"
              class="ml-2 mr-4"
            ></v-icon>
            <v-list-item-title v-text="item.title"></v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <v-menu
        transition="slide-y-transition"
        rounded="0"
        content-class="elevation-1"
        offset-y
        center
        bottom
        open-on-hover
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn text v-bind="attrs" v-on="on">
            <span>
              About
            </span>
            <v-icon>
              mdi-chevron-down
            </v-icon>
          </v-btn>
        </template>
        <v-list dense>
          <v-list-item v-for="(item, i) in aboutMenu" :key="i">
            <v-icon
              v-html="item.icon"
              color="light-gray"
              class="ml-2 mr-4"
            ></v-icon>
            <v-list-item-title v-text="item.title"></v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>

      <v-btn text class="mr-16">
        Blog
      </v-btn>
      <v-btn
        x-small
        rounded
        elevation="0"
        color="primary"
        class="align-self-start ml-10 mr-16 mt-1"
        v-text="'login'"
      ></v-btn>
    </div>
    <v-btn
      v-if="$vuetify.breakpoint.smAndDown"
      @click="openDrawer"
      icon
      class="mr-2"
    >
      <v-icon color="">mdi-menu</v-icon>
    </v-btn>
  </v-app-bar>
</template>

<script>
export default {
  data() {
    return {
      logo: require("./../assets/logo.png")
    };
  },

  methods: {
    openDrawer() {
      this.$store.commit("ui/setDrawer", true);
    }
  },

  computed: {
    isShrink() {
      return this.$route.name == "home" && this.$vuetify.breakpoint.mdAndUp;
    },
    courseMenu() {
      return this.$store.getters["ui/courseMenu"];
    },
    aboutMenu() {
      return this.$store.getters["ui/courseMenu"];
    }
  }
};
</script>

<style scoped>
.theme--light.v-list-item--active::before,
.theme--light.v-list-item--active::before {
  opacity: 0 !important;
}
</style>
