<template>
  <div style="width: 100%; min-height: 100vh;">
    <!-- cover image -->
    <v-img
      :src="cover"
      height="50vh"
      gradient="to top, rgba(0,0,0,1), rgba(255,255,255,0)"
    >
      <v-sheet
        color="grey lighten-2"
        style="opacity: 0.6;"
        class=" mt-4 mr-4 pa-1 rounded-xl view-count"
      >
        <div class="text-body-2">
          {{ count }}
          <v-icon>mdi-eye</v-icon>
        </div>
      </v-sheet>
      <div
        class="text-h6 mr-4 mb-2 white--text font-weight-light category-text"
        v-text="category"
      ></div>
    </v-img>

    <!-- title -->
    <div class="mx-md-16 mx-2">
      <v-row class="mb-8">
        <v-col cols="12" md="6" class="d-flex flex-column justify-start">
          <div
            class="text-md-h3 text-h5 text--primary mb-4"
            v-text="name"
          ></div>
          <div
            class="text-md-h6 text-body-2 font-weight-light text--secondary"
            v-text="subTitle"
          ></div>
          <!-- description  -->
          <v-row class="mt-16" justify="center">
            <v-col
              cols="10"
              class="text-body-1 font-weight-regular"
              v-html="description"
            >
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="8" offset="2" offset-md="3" md="3">
          <div class="text-caption text-right mb-8">
            Published in: {{ date }}
          </div>
          <v-card width="100%" elevation="1">
            <v-card-text>
              <v-row>
                <v-col cols="2">
                  <v-icon color="">mdi-chart-line</v-icon></v-col
                >
                <v-col cols="5"> Level </v-col>
                <v-col cols="5">
                  <v-rating dense readonly v-model="level">
                    <template v-slot:item="props">
                      <v-icon
                        small
                        color="secondary"
                        large
                        @click="props.click"
                      >
                        {{
                          props.isFilled ? "mdi-circle" : "mdi-circle-outline"
                        }}
                      </v-icon>
                    </template>
                  </v-rating>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="2">
                  <v-icon color="">mdi-camera-timer</v-icon>
                </v-col>
                <v-col cols="5">Duration</v-col>
                <v-col cols="5"> {{ duration }}</v-col>
              </v-row>
              <v-row>
                <v-col cols="2">
                  <v-icon color="">mdi-teach</v-icon>
                </v-col>
                <v-col cols="5">Lectures</v-col>
                <v-col cols="5"> {{ lectures }}</v-col>
              </v-row>
              <v-row>
                <v-col cols="2">
                  <v-icon color="">mdi-school-outline</v-icon>
                </v-col>
                <v-col cols="5">Enrolled</v-col>
                <v-col cols="5"> {{ enrolled }}</v-col>
              </v-row>
              <v-row>
                <v-col cols="2">
                  <v-icon color="">mdi-currency-usd-circle-outline</v-icon>
                </v-col>
                <v-col cols="5">Price</v-col>
                <v-col cols="5"> {{ price }}</v-col>
              </v-row>
            </v-card-text>
            <v-card-actions class="d-flex justify-center">
              <v-btn outlined color="primary"> Enroll now </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>

      <div class="text-h6 font-weight-regular">
        By completing this course you will ..
      </div>
      <v-row justify="center" class="mx-10 mb-8">
        <v-col cols="6" md="3" v-for="(item, i) in features" :key="i">
          <v-card
            class="rounded-lg"
            color="#EEEEEE"
            max-width="100%"
            height="10rem"
            max-height="10rem"
            elevation="0"
          >
            <v-card-text class="d-flex flex-column align-center">
              <v-icon x-large color="secondary" class="mb-6"
                >mdi-check-outline</v-icon
              >
              <div class="text-body-2 text-center" v-text="item"></div>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>

      <!-- slide -->
      <v-row justify="center">
        <v-col cols="12" md="12">
          <div style="position: relative;">
            <v-slide-group class="pa-md-4" center-active show-arrows dark>
              <template v-slot:next>
                <v-btn small icon>
                  <v-icon color="blue-grey darken-2" style="z-index: 2;">
                    mdi-chevron-right
                  </v-icon>
                </v-btn>
              </template>
              <template v-slot:prev>
                <v-btn small icon>
                  <v-icon color="blue-grey darken-2" style="z-index: 2;">
                    mdi-chevron-left
                  </v-icon>
                </v-btn>
              </template>
              <v-slide-item
                v-for="(item, i) in screenshots"
                :key="i"
                class="ma-4 rounded-lg"
                style="overflow: hidden;"
              >
                <v-img :src="item" width="200" height="200"></v-img>
              </v-slide-item>
            </v-slide-group>
            <div v-if="$vuetify.breakpoint.mdAndUp" class="slide-left"></div>
            <div v-if="$vuetify.breakpoint.mdAndUp" class="slide-right"></div>
          </div>
        </v-col>
      </v-row>
      <!-- instructor -->
      <v-row justify="center" class="my-8">
        <v-col cols="8" md="4">
          <div
            class="text-center text-body-1 font-weight-light font-italic mb-2"
          >
            Meet the instructor
          </div>
          <v-card class="" elevation="2" max-width="">
            <v-card-text class="d-flex flex-column align-center">
              <v-avatar class="mb-2">
                <img :src="instructor.img" alt="" />
              </v-avatar>
              <div
                class="text--secondary text-center mb-8 font-weight-light"
                v-text="instructor.bio"
              ></div>
              <div
                class="text--primary text-body-2 font-weight-light"
                v-text="instructor.msg"
              ></div>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>

      <!-- reviews -->
      <div class="text-h6 font-weight-regular">Reviews</div>
      <v-row v-for="(item, i) in reviews" :key="i">
        <v-col cols="12" md="6" order="2" order-md="1">
          <div class="d-flex ma-2 mb-4">
            <div class="d-inline mr-4">
              <v-avatar size="50">
                <img :src="item.img" />
              </v-avatar>
            </div>
            <div class="d-inline">
              <v-sheet color="grey lighten-4" class="rounded-lg pa-2">
                <div
                  class="text-body-2 font-weight-light mb-6"
                  v-text="item.comment"
                ></div>
                <div class="d-flex mx-2">
                  <div
                    class="text--secondary d-inline text-body-2 mr-auto"
                    v-text="item.name"
                  ></div>
                  <div
                    class="text--disabled text-caption d-inline"
                    v-text="item.date"
                  ></div>
                </div>
              </v-sheet>
            </div>
          </div>
        </v-col>
        <v-col
          cols="12"
          md="6"
          v-if="i == 0"
          order="1"
          order-md="2"
          class="d-flex align-center flex-column"
        >
          <div class="text-body-2 text-center">Community rating</div>
          <v-rating
            readonly
            v-model="rate.value"
            color="primary"
            background-color="orange lighten-3"
            class="d-inline"
            dense
          ></v-rating>
          <div class="text-body-1 d-inline">{{ `(${rate.count})` }}</div>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
