      this.description =
        "Non magna culpa occaecat magna fugiat proident aliquip in quis est eiusmod occaecat mollit. Sunt aliquip dolor consectetur incididunt non ex mollit excepteur voluptate. Proident pariatur elit consectetur est enim voluptate voluptate sunt amet. Id sint commodo dolor commodo Lorem Lorem nulla ex aliqua excepteur cillum. Fugiat adipisicing nulla eu et aliquip labore.";
      this.screenshots = [
        require("./../../assets/blog01.jpg"),
        require("./../../assets/blog02.jpg"),
        require("./../../assets/blog03.jpg"),
        require("./../../assets/blog04.jpg"),
        require("./../../assets/blog05.jpg"),
        require("./../../assets/blog05.jpg"),
        require("./../../assets/blog05.jpg")
      ];
      this.instructor = {
        img: require("./../../assets/blog05.jpg"),
        bio: "Leader and expert in software, web technologies",
        msg:
          "We'll have a great journey to learn all technologies in the Non exercitation eu consequat sunt adipisicing officia exercitation."
      };
      this.rate = { value: 4.5, count: 40 };
      this.reviews = [
        {
          comment:
            "Aliquip consequat enim est et ea ut laborum non nostrud occaecat amet. Laboris sunt quis fugiat cupidatat velit dolor et a xcepteur duis officia non. Incididunt quis ut ex tempor cupidatat qui eu ea  voluptate esse.",
          name: "alexandar maximan",
          img: require("./../../assets/avatar.png"),
          date: "01-01-2020"
        },
        {
          comment:
            "Aliquip consequat enim est et ea ut laborum non nostrud occaecat amet. Laboris sunt quis fugiat cupidatat velit dolor et a xcepteur duis officia non. Incididunt quis ut ex tempor cupidatat qui eu ea  voluptate esse.",
          name: "dev ed",
          img: require("./../../assets/avatar.png"),
          date: "01-01-2020"
        }
      ];
    }
  },

  created() {
