  }
};
</script>

<style>
.category-text {
  position: absolute;
  bottom: 0;
  right: 0;
}

.view-count {
  position: absolute;
  top: 0;
  right: 0;
}

.slide-right {
  position: absolute;
  top: 0;
  right: 0;
  width: 250px;
  height: 250px;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    270deg,
    rgba(255, 255, 255, 1) 50%,
    rgba(255, 255, 255, 0) 100%
  );
}

.slide-left {
  position: absolute;
  top: 0;
  left: 0;
  width: 250px;
  height: 250px;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 1) 50%,
    rgba(255, 255, 255, 0) 100%
  );
}
</style>
